<template>
    <view>
        <view v-for="(item, index) in  bodyData.content"
        v-if="item.userId != ovoSelf.userId"
         :style="{
            'opacity': item.opacity
        }">

            <user-card :ovoUserInfo="item" @click="$refs.userPopup.open(item)"></user-card>
            <view style="height: 10rpx;"></view>
        </view>
        <view style="height: 50rpx;" v-if="bodyData.length == 0"></view>

        <!-- 正在获取用户信息 -->
        <view v-if="isPushing">
            <view style="height: 30rpx;"></view>
            <view style="text-align: center;">
                <image :src="$kt.file.byPath('kantboot/icon/loading.svg')" class="loading-icon" mode="widthFix"></image>
            </view>
        </view>

        <view v-if="isPushFinall && !isPushEnd && bodyData.content.length > 7"
            style="padding: 30rpx 0 30rpx 0;text-align: center;color: #999;font-size: 30rpx;">下滑可能获取得到更多推荐~</view>
        <view v-if="isPushFinall && !isPushEnd && bodyData.content.length > 7" style="height: 40rpx;"></view>

        <view v-if="isPushFinall && isPushEnd && pushEndShow && bodyData.content.length > 7"
            style="padding: 30rpx 0 30rpx 0;text-align: center;color: #999;font-size: 30rpx;">
            获取不到更多了，一分钟后再试试吧~
        </view>
        <view v-if="isPushFinall && isPushEnd && !pushEndShow"
            style="padding: 30rpx 0 30rpx 0;text-align: center;color: #999;font-size: 30rpx;">
            没有更多了~
        </view>

        <user-popup ref="userPopup"></user-popup>
    </view>
</template>

<script>

export default {

    props: {
        screenTop: {
            type: Number,
            default: 0
        },
        isIn: {
            type: Boolean,
            default: false
        },
    },

    data() {
        return {
            isPushing: false,
            // 是否添加完成
            isPushFinall: true,
            isPushEnd: false,
            pushEndShow: true,
            bodyData: {
                content: [],
                totalElements: 0
            },
            ovoSelf: {
                userId: ""
            },
            requestParam: {
                pageNumber: 1,
                sortField: "gmtCreate",
                sortOrderBy: "desc",
            },
            // 请求次数
            requestCount: 0,
            timer: null,
            isTimering: false,
        }
    },
    created() {
        this.timer = setInterval(() => {
            // 获取当前页面路径
            var pages = getCurrentPages();
            var page = pages[pages.length - 1];
            var route = page.route;

            if(route!='pages/body/static'){
                return false;
            }

            if(!this.isIn){
                return false;
            }

            console.log("--")

            if (this.screenTop > this.$kt.util.rpxToPx(200) && !this.isTimering) {
                this.isTimering = false;
                return false;
            }
            this.isTimering = true;
            this.findGreater();
        }, 30000);
        this.ovoSelf = this.$ovo.getSelf();
        // 如果有缓存数据
        if (uni.getStorageSync('userListOfRecommend')) {
            this.bodyData.content = uni.getStorageSync('userListOfRecommend');
        }
        // 如果有缓存时间
        if (uni.getStorageSync('userListOfRecommendTime')) {
            var time = uni.getStorageSync('userListOfRecommendTime');
            var nowTime = new Date().getTime();
            // 如果缓存时间超过3天
            if (nowTime - time > 1000 * 60 * 60 * 24 * 3) {
                uni.removeStorageSync('userListOfRecommend');
                uni.removeStorageSync('userListOfRecommendTime');
            }
        } else {
            uni.removeStorageSync('userListOfRecommend');
            uni.removeStorageSync('userListOfRecommendTime');
        }

        // 如果缓存数据不足10条,则继续获取
        if (this.bodyData.content.length < 10) {
            this.getBodyData();
        }

        uni.$on("pageIndexOnScrollToLower", () => {
            if (this.isPushFinall) {
                this.requestParam.pageNumber++;
                this.findLess();
            }
        })
        uni.$on("loginSuccess", () => {
            this.isPushFinall = true;
            this.isPushing = false;
            this.isPushEnd = false;
            this.findGreater();
        });
    },
    methods: {
        itemClick(res) {
        },
        findGreater() {
            this.$kt.request.request({
                api: this.$api.business.ovo.userBind.findGreater,
                data: {
                    userId: this.bodyData.content[0].userId
                },
                stateSuccess: (res) => {
                    for (var i = res.data.content.length-1; i >=0 ; i--) {
                        this.bodyData.content.unshift(res.data.content[i]);
                    }
                    // 将this.bodyData.content的前100条数据缓存
                    if (this.bodyData.content.length > 0) {
                        uni.setStorageSync('userListOfRecommend', this.bodyData.content.slice(0, 100));
                    }
                }
            })
        },
        async findLess() {
            if (!this.isPushFinall || this.isPushing || this.isPushEnd) {
                if (this.pushEndShow) {
                    setTimeout(() => {
                        this.pushEndShow = false;
                    }, 1000);
                    setTimeout(() => {
                        this.pushEndShow = true;
                    }, 60000);
                }

                return false;
            }

            this.isPushFinall = false;
            this.isPushing = true;
            var jsonArr = [];
            var resData = null;
            await new Promise((resolve, reject) => {
                this.$kt.request.request({
                    api: this.$api.business.ovo.userBind.findLess,
                    data: {
                        userId: this.bodyData.content[this.bodyData.content.length - 1].userId,
                    },
                    stateSuccess: (res) => {
                        jsonArr = res.data.content;
                        resData = res.data;
                        resolve();
                    }
                })
            });


            if (this.bodyData.content.length >= resData.totalElements || resData.content.length == 0) {
                this.isPushEnd = true;
                setTimeout(() => {
                    this.isPushEnd = false;
                }, 60000);

                setTimeout(() => {
                    this.isPushing = false;
                }, 500);
                setTimeout(() => {
                    this.isPushFinall = true;
                }, 500);
                this.requestParam.pageNumber--;
                return false;
            }
            this.isPushing = false;

            // 如果jsonArr中有userId和bodyData的userId相同，则不添加
            for (var i = 0; i < jsonArr.length; i++) {
                var isExist = false;
                for (var j = 0; j < this.bodyData.content.length; j++) {
                    if (jsonArr[i].userId == this.bodyData.content[j].userId) {
                        isExist = true;
                        break;
                    }
                }
                if (!isExist) {

                    var json = { ...jsonArr[i], 'opacity': 0 };
                    this.bodyData.content.push(json);

                    await new Promise((resolve, reject) => {

                        var interVal = setInterval(() => {
                            this.bodyData.content[this.bodyData.content.length - 1].opacity += 0.1;
                            this.$forceUpdate();
                            if (this.bodyData.content[this.bodyData.content.length - 1].opacity >= 1) {
                                clearInterval(interVal);
                                resolve("")
                            }
                        }, 50);

                    });
                }
            }

            // 如果小于100条
            if (this.bodyData.content.length < 100) {
                // 将bodyData.content放入缓存中
                uni.setStorageSync("ovoUserRecommendList", this.bodyData.content);
                // 将放入缓存的时间放入缓存中
                uni.setStorageSync("ovoUserRecommendListTime", new Date().getTime());
            }

            this.isPushFinall = true;
            this.requestCount++;

        },
        async getBodyData() {
            if (!this.isPushFinall || this.isPushing || this.isPushEnd) {
                if (this.pushEndShow) {
                    setTimeout(() => {
                        this.pushEndShow = false;
                    }, 1000);
                    setTimeout(() => {
                        this.pushEndShow = true;
                    }, 60000);
                }

                return false;
            }

            this.isPushFinall = false;
            this.isPushing = true;
            var jsonArr = [];
            var resData = null;
            await new Promise((resolve, reject) => {
                this.$kt.request.request({
                    api: this.$api.business.ovo.userBind.getRecommendList,
                    data: this.requestParam,
                    stateSuccess: (res) => {
                        jsonArr = res.data.content;
                        resData = res.data;
                        resolve();
                    }
                })
            });


            if (this.bodyData.content.length >= resData.totalElements || resData.content.length == 0) {
                this.isPushEnd = true;
                setTimeout(() => {
                    this.isPushEnd = false;
                }, 60000);

                setTimeout(() => {
                    this.isPushing = false;
                }, 500);
                setTimeout(() => {
                    this.isPushFinall = true;
                }, 500);
                this.requestParam.pageNumber--;
                return false;
            }
            this.isPushing = false;

            // 如果jsonArr中有userId和bodyData的userId相同，则不添加
            for (var i = 0; i < jsonArr.length; i++) {
                var isExist = false;
                for (var j = 0; j < this.bodyData.content.length; j++) {
                    if (jsonArr[i].userId == this.bodyData.content[j].userId) {
                        isExist = true;
                        break;
                    }
                }
                if (!isExist) {

                    var json = { ...jsonArr[i], 'opacity': 0 };
                    this.bodyData.content.push(json);

                    await new Promise((resolve, reject) => {

                        var interVal = setInterval(() => {
                            this.bodyData.content[this.bodyData.content.length - 1].opacity += 0.1;
                            this.$forceUpdate();
                            if (this.bodyData.content[this.bodyData.content.length - 1].opacity >= 1) {
                                clearInterval(interVal);
                                resolve("")
                            }
                        }, 50);

                    });
                }
            }

            // 如果小于100条
            if (this.bodyData.content.length < 100) {
                // 将bodyData.content放入缓存中
                uni.setStorageSync("ovoUserRecommendList", this.bodyData.content);
                // 将放入缓存的时间放入缓存中
                uni.setStorageSync("ovoUserRecommendListTime", new Date().getTime());
            }

            this.isPushFinall = true;
            this.requestCount++;

        }
    },
    watch: {

    },
}

</script>

<style lang="scss" scoped>
.loading-icon {
    width: 70rpx;
    height: 70rpx;
    filter: invert(90%);
    // 不停旋转的动画
    animation: rotate 3s linear infinite;
}

@keyframes rotate {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(360deg);
    }
}
</style>